body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
}

ol, ul {
  padding-left: 0px;
}

p#content {
  padding: 30px;
}

p#content2 {
  padding-top: 20px;
  padding-left: 50px;
  padding-right: 50px;
}

.div {
  padding: 0;
}

.medium {
  width:40vw;
}
.inlineblock {
  display: inline-block;
  margin: 20px;
  padding: 0px;
}

.smol {
  width:50px;
}

.floatLeft {
  display: inline-block;
  padding-top: 20px;
  padding-left: 50px;
  padding-right: 50px;
}

.split {
  height: 100%;
  width: 50%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
}

.left {
  left: 0;
}

.right {
  right: 0;
}

.halfScreenPicture {
  height:100vh;
  width:50vw;
  display: block;
  margin: 0;
  padding: 0;
  object-fit: cover;
  float: left;
}

.circlePic {
  height:200px;
  width:200px;
  padding: 25px;
  border-radius: 50%;
  object-fit: cover;
}

.smallLogo {
  height:100px;
  width:100px;
  padding: 0px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 1px 1px 1px 1px #888888;
}

.content {
  float: right;
  height:100vh;
  width:50vw;
}

.float{
  position:fixed;
  width:120px;
  height:50px;
  left:40px;
  background-color:#424242;
  color:#FFF;
  text-align:center;
  z-index: 2;
  
}

.f1{
  bottom:calc(50vh - 120px);
}

.f2{
  bottom:calc(50vh - 60px);
}

.f3{
  bottom:50vh;
}

.f4{
  bottom:calc(50vh + 60px);
}

.f5{
  bottom:calc(50vh + 120px);
}


ol, ul {
	padding: 20px;
}

.yikes{
	max-width:1400px
}

.imgStyle{
	width:100%
}

.smolImgStyle {
	width:50px
}

.floatRight{
  position:fixed;
  width:120px;
  height:50px;
  right:40px;
  background-color:#424242;
  color:#FFF;
  text-align:center;
  z-index: 2;
  bottom: 40px;
}

img#a {
    width: 100%;
    height: 100%;
}

.floatTopRight{
  position:fixed;
  width:120px;
  height:50px;
  top:10px;
  background-color:#FFFFFF;
  color:#000;
  text-align:center;
  z-index: 2;
  
}

.f1 {
  right:10px;
}

.f2 {
  right:140px;
}

.f3 {
  right:270px;
}

.f4{
    right:400px;
}

.smolLogo {
  height:50px;
  width:50px;
  padding: 0px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 1px 1px 1px 1px #888888;
}

.lst {
	list-style-type: none;
}
